import './index.scss'
let disabledOP = ['=', 'C', 'del']
let displayBox = document.querySelector('.display')
let btnList = document.querySelectorAll('.calc input[type="button"]')
let computedBtn = document.querySelector('.computed')
let clearBtn = document.querySelector('.clear')
let delBtn = document.querySelector('.del')
let operas = ''


// mid notation to last notation
// 3 + 4 × 5 - 6 => 3 4 + 5 × 6 

// combine normalNumbers
// ['(', '1', '2' ,+', '4', ')', '*', '5', '-', '6']
// =>   ['(', '12',+', '4', ')', '*', '5', '-', '6']
const normalNumbers = (list) => {
  return list.match(/[^\d()]+|[\d.]+/g)
}

const notation = (list) => {
  let output = [],
    opStack = [],
    opList = [['+', '-'], ['*', '/']],
    getOpPriority = (op) => {
      let i = -1
      for (let [index, value] of opList.entries()) {
        if ((Array.isArray(value) && value.indexOf(op) > -1) || value === op) {
          i = index
          break
        }
      }
      return i
    }

  list.forEach(item => {
    if (/\d/.test(item)) {
      output.push(item)
    } else {
      if (getOpPriority(item) > -1) {
        if (opStack.length === 0 || getOpPriority(item) > getOpPriority(opStack[opStack.length - 1])) {
          opStack.push(item)
        } else {
          while (true) {
            let popOP = opStack.pop()
            output.push(popOP)
            opStack.push(item)
            if (opStack.length === 0 || getOpPriority(item) <= getOpPriority(opStack[opStack.length - 1])) {
              break
            }
          }
        }
      } else {
        if (item === ')') {
          while (true) {
            let popOP = opStack.pop()
            if (popOP !== '(') {
              output.push(popOP)
            } else {
              break
            }
          }
        } else {
          opStack.push(item)
        }
      }
    }
  })

  while (opStack.length != 0) {
    output.push(opStack.pop())
  }

  return output
}

// 12 3 4 + * 6 - 8 2 / +
const calc = (publicity) => {
  let sum = 0
  let stack = publicity,
    opList = [['+', '-'], ['*', '/']],
    getOpPriority = (op) => {
      let i = -1
      for (let [index, value] of opList.entries()) {
        if ((Array.isArray(value) && value.indexOf(op) > -1) || value === op) {
          i = index
          break
        }
      }
      return i
    }


  let tmp = []
  while (stack.length !== 0) {
    let item = stack.shift()
    if (getOpPriority(item) > -1) {
      let op1 = tmp.pop()
      let op2 = tmp.pop()
      let result = 0
      switch (item) {
        case '+':
          result = op2 + op1
          break
        case '-':
          result = op2 - op1
          break
        case '*':
          result = op2 * op1
          break
        case '/':
          result = op2 / op1
        default: break
      }
      tmp.push(result)
    } else {
      tmp.push(parseInt(item))
    }

  }
  return tmp[0]
}


Array.from(btnList).forEach(item => {
  item.addEventListener('click', e => {
    let op = e.target.value
    if (disabledOP.includes(op)) return
    displayBox.innerHTML += op
    operas += op
  })
})

computedBtn.addEventListener('click', e => {
  displayBox.innerHTML = calc(notation(normalNumbers(operas)))
  operas = ''
})

clearBtn.addEventListener('click', e => {
  displayBox.innerHTML = operas = ''
})

delBtn.addEventListener('click', e => {
  displayBox.innerHTML = displayBox.innerHTML.substring(0, displayBox.innerHTML.length - 1)
  operas = operas.substring(1, operas.length - 1)
})
